"use client";

import React, { useRef } from 'react';
import Image from "next/image";

const izq = "/icons/izquierdaC.svg";
const der = "/icons/derechaC.svg";
const coraR = "/icons/coraR.svg";
const coraB = "/icons/coraB.svg";
const star = "/icons/star 5.svg";
const pic4 = "/pic4.jpg";
const pic5 = "/pic5.jpg";
const pic6 = "/pic6.jpg";

const juegos = [
  {
    title: "Cat Quest II",
    image: `${pic4}`,
    tags: ["RPG"],
    rating: 5.0,
  },
  {
    title: "Cat Quest II",
    image: `${pic4}`,
    tags: ["RPG"],
    rating: 3.0,
  },
  {
    title: "Cat Quest II",
    image: `${pic4}`,
    tags: ["RPG"],
    rating: 3.5,
  },
  {
    title: "Arcadegeddon",
    image: `${pic5}`,
    tags: ["AVENTURA", "RPG"],
    rating: 4.5,
  },
  {
    title: "River City Girls",
    image: `${pic6}`,
    tags: ["RPG"],
    rating: 5.0,
  },
];

const JuegosGratis = () => {
  const scrollRef = useRef<HTMLDivElement>(null);

  const scroll = (dir: "left" | "right") => {
  if (scrollRef.current) {
    const scrollAmount = 320 + 24; // tarjeta + gap
    scrollRef.current.scrollBy({
      left: dir === "left" ? -scrollAmount * 3 : scrollAmount * 3,
      behavior: "smooth",
    });
  }
};

  return (
    <div className="w-auto bg-deep rounded-3xl text-white py-10 px-6 overflow-hidden">
      <div className="max-w-7xl mx-auto relative">
        {/* Header */}
        <div className="flex justify-between items-center mb-6">
          <h2 className="text-xl font-primary">Juegos gratis</h2>
          <div className="flex items-center gap-2">
            <a href="#" className="text-primary text-xl hover:underline">
              Ver todos
            </a>
            <button onClick={() => scroll("left")}>
              <Image src={izq} alt="izquierda" width={40} height={40} />
            </button>
            <button onClick={() => scroll("right")}>
              <Image src={der} alt="derecha" width={40} height={40} />
            </button>
          </div>
        </div>

        {/* Carrusel */}
        <div
            ref={scrollRef}
            className="flex gap-6 overflow-hidden scroll-smooth"
            style={{ width: `${320 * 3 + 6 * 2}px` }} // 3 tarjetas + 2 gaps de 24px aprox
            >
            {juegos.map((juego, i) => (
                <div
                key={i}
                className="w-[310px] flex-shrink-0 bg-deep rounded-xl overflow-hidden shadow-md relative"
                >
              {/* Imagen con Ã­cono */}
              <div className="relative ">
                <img
                  src={juego.image}
                  alt={juego.title}
                  className="w-full h-230px] object-cover rounded-4xl "
                />
                <div className="absolute top-2 right-2">
                  <Image src={coraR} alt="heart" width={56} height={56} />
                </div>
              </div>

              {/* Contenido */}
              <div className="p-4">
                <div className="flex gap-2 mb-2">
                  {juego.tags.map((tag, j) => (
                    <span
                      key={j}
                      className="bg-categorico text-xs px-2 py-1 rounded-md text-white"
                    >
                      {tag}
                    </span>
                  ))}
                </div>
               <div className="flex justify-between items-center mb-2">
                <h3 className="text-base font-semibold">{juego.title}</h3>
                <div className="flex items-center gap-1">
                    {[...Array(5)].map((_, i) => (
                    <Image
                        key={i}
                        src={star}
                        alt="estrella"
                        width={14}
                        height={14}
                    />
                    ))}
                    <span className="text-xs font-medium">{juego.rating.toFixed(1)}</span>
                </div>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default JuegosGratis;
